.footer {
  max-width: 100%;
  background-color: var(--dark-green);
  color: var(--primary-white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 66px;
  padding-bottom: 64px;
  padding-inline: 24px;
  box-sizing: border-box;
}

.footerInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footerLinks {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  transition: all 3ms ease-in;
  margin-block: 24px;
}

.footerLinks .link:hover {
  color: var(--coral-color);
}

.link {
  text-decoration: none;
  color: inherit;
  margin: 5px 0;
}

.location {
  font-style: normal;
  color: var(--primary-white);
  opacity: 0.6;
  text-align: center;
  margin-bottom: 40px;
}

.socialContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.socialLinks {
  display: flex;
  gap: 1rem;
  margin-bottom: 16px;
  cursor: pointer;
}

.copyright {
  opacity: 0.6;
  align-items: center;
  text-align: center;
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .footer {
    padding-inline: 40px;
  }
  .footerInfo {
    flex-direction: row;
    justify-content: space-between;
    width: 98%;
  }
  .socialContent {
    flex-direction: row;
    justify-content: space-between;
    width: 98%;
  }
  .footerLinks {
    justify-content: flex-start;
  }

  .location {
    text-align: right;
  }
}

@media (min-width: 960px) {
  .footer {
    flex-direction: row;
    padding-inline: 5rem;
    height: 200px;
    padding-block: 48px;
  }
  .footerInfo {
    justify-content: flex-start;
    gap: 60px;
  }
  .location {
    text-align: left;
  }

  .socialContent {
    flex-direction: column;
    align-items: flex-end;
  }
  .socialLinks {
    margin-bottom: 40px;
  }
}
